import { Provider } from '@angular/core';
import { WalletManagementConfig } from './wallet-management.config';
import { WalletManagementDefaultConfig } from './default.config';

export function walletManagementConfigProvider(config = WalletManagementDefaultConfig): Provider {
	return {
		provide: WalletManagementConfig,
		useValue: config
	};
}
